.header__menu{
    background-color: #2B2B2B;
    height: vw(80);
    display: flex;
    align-items: center;
}

.menu__container{
    display: flex;
    justify-content: space-between;
    align-items: center;

    .search{
        align-self: flex-end;
        transition: .3s;
        margin-left: vw(80);
        &.disable{
            border: none;

            input{
                width: 0;
            }

            .search__ico{
                margin-left: vw(180);
            }

            
    
        }

        input{
            transition: width .3s;
        }
    }
}

.menu__left{
    display: flex;
    align-items: center;
}

a.menu__logo{
    width: vw(126);
    height: vw(62);

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        align-self:flex-start
    }
}

.menu__nav{
    width: vw(544);
    display: flex;
    justify-content: space-between;
    margin-left: vw(45);

    a.nav__item{
        font-family: 'Montserrat', sans-serif;
        color: white;
        font-size: vw(17);
        transition: .3s color;

        &:hover{
            color: rgba(#fff, 0.6);
        }
    }
}

.menu__burger{
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: vw(45);
    height: vw(35);
    cursor: pointer;

    span{
        position: relative;
        height: vw(1.5);
        width: 100%;
        background: #fff;

        &::after{
            content: '';
            position: absolute;
            height: vw(1.5);
            width: 100%;
            background: #fff;
            top: vw(12);
            left: 0;
        }

        &::before{
            content: '';
            position: absolute;
            height: vw(1.5);
            width: 100%;
            background: #fff;
            top: vw(-12);
            left: 0;
        }
    }
}

.menu__right{
    width: vw(400);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__intro{
    position: relative;
    background: url('../img/intro.png') no-repeat center;
    background-size: 100%;
    height: vw(620);
    width: 100%;
}

.intro__content{
    width: vw(1155);
    height: vw(378);
    background: linear-gradient(90.18deg, #222222 3.44%, rgba(43, 43, 43, 0.3) 55.56%, rgba(255, 255, 255, 0.05) 81.76%);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: vw(55);

    p.title{
        text-transform: uppercase;
        font-size: vw(47);
        color: white;
        font-family: 'Montserrat', sans-serif;
        font-weight: 662;
        line-height: 120%;

        span{
            color: #57C54E;
        }
    }

    p.text{
        font-size: vw(27);
        color: white;
        font-family: 'Montserrat', sans-serif;
        font-weight: 400;
        margin-top: vw(40);
    }

    button.intro{
        width: vw(300);
        height: vw(71);
        border-radius: vw(45);
        background: linear-gradient(90deg, #067C48 0%, #3CBA92 100%);
        cursor: pointer;
        outline: none;
        border: none;
        text-transform: uppercase;
        color: white;
        font-family: 'Montserrat', sans-serif;
        font-size: vw(17);
        font-weight: 500;
        margin-top: vw(30);
    }
}

.intro__btn{
    position: absolute;
    bottom: vw(60);
    right: vw(258);
}

.intro__btn__images{
    width: vw(50);
    height: vw(51);

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}