.reviews__container{
    padding: vw(65) 0;

    p.header{
        text-transform: uppercase;
        font-size: vw(37);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
    }
}

.reviews__wrapper{
    display: flex;
    align-items: flex-start;
}

.reviews__left{
    display: flex;
    flex-direction: column;
}

.reviews__item{
    position: relative;
    padding-top: vw(35);
    padding-left: vw(92);
    margin-top: vw(45);

    .reviews__images{
        width: vw(77);
        height: vw(77);
        position: absolute;
        top: 0;
        left: 0;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    p.data__text{
        font-size: vw(15);
        font-family: 'Montserrat', sans-serif;

        span{
            font-weight: 600;
        }
    }

    .reviews{
        p.data__text:last-child{
            padding-top: vw(15);
        }
    }

    .stars__images{
        width: vw(114);
        height: vw(15);
        margin: vw(10) 0;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .reviews__comment{
        width: vw(423);
        padding: vw(30);
        border-radius: vw(20);
        background-color: #EFEFEF;
        color: black;
        font-size: vw(15);
        font-family: 'Montserrat', sans-serif;
    }

}

.reviews__right{
    display: flex;
    flex-direction: column;
    margin-left: vw(80);

    p.title{
        font-size: vw(30);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
    }

    button{
        width: vw(236);
        height: vw(56);
        border-radius: vw(55);
        background-color: #4A8854;
        font-family: 'Montserrat', sans-serif;
        color: white;
        font-size: vw(17);
        text-transform: uppercase;
        margin-top: vw(30   );
    }
}

.input__wrapp{
    margin-top: vw(40);

    p.input__name{
        font-size: vw(15);
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        margin-bottom: vw(15);
    }

    textarea{
        width: vw(600);
        height: vw(180);
        border: vw(1) solid #000000;
        border-radius: vw(15);
        outline: none;
        padding: vw(20);
        font-size: vw(15);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        resize: none;
    }

    input{
        outline: none;
        border-radius: vw(15);
        height: vw(30);
        border: vw(1) solid #000000;
        font-size: vw(15);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        padding: 0 vw(20);
    }

    select{
        width: vw(217);
        height: vw(25);
        border: vw(1) solid #000000;
        outline: none;
        font-size: vw(15);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
    }
}

.input__double{
    display: flex;
    align-items: center;

    .input__wrapp:first-child{

        input{
            width: vw(163);
        }
    }

    .input__wrapp:last-child{

        input{
            width: vw(254);
        }

        margin-left: vw(30);
    }
}