.swiper-slide{
    background: #262626;
    width: vw(570);
    height: vw(376);

    .slide__images{
        height: vw(376);
        width: vw(351);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .slide__info{
        position: absolute;
        right: 0;
        top: 0;
        height: vw(278);
        width: vw(221);
        padding: vw(22) vw(15);

        p.info{
            padding-top: vw(30);
            color: white;
            font-size: vw(17);
            font-family: 'Montserrat', sans-serif;
            font-weight: 400;

            &:first-child{
                padding-top: 0;
            }
        }

        a.more-info{
            display: flex;
            align-items: center;
            padding-top: vw(65);
            color: white;
            font-size: vw(17);
            font-family: 'Montserrat', sans-serif;
            font-weight: 400;
            position: relative;

            &::after{
                content: '';
                position: absolute;
                bottom: vw(-5);
                left: 0;
                width: vw(125);
                height: vw(1);
                background-color: #fff;
            }

            .mi__images{
                width: vw(18);
                height: vw(6);
                margin-left: vw(5);

                img{
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}

.bestproject__container{
    padding: vw(70) 0;

    p.header{
        font-size: vw(37);
        text-transform: uppercase;
        margin-bottom: vw(40);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
    }
}

.slider__wrapper{
    position: relative;

    .swiper-button-prev{
        position: absolute;
        left: vw(-60);
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        width: vw(42);
        height: vw(28);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .swiper-button-next{
        position: absolute;
        right: vw(-60);
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        width: vw(42);
        height: vw(28);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}