.quiz{
    width: 100%;
    /* position: relative;
    display: grid;  */
    display: flex;
    align-items: flex-start;
}

.quiz__nav{
    width: fit-content;
    height: fit-content;
    position: relative;
    z-index: 1;
    
    .quiz__nav__images{
        width: vw(750);
        height: vw(1005);
        position: relative;
        z-index: 5;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .quiz__pagination{
        z-index: 10;
        position: absolute;
        top: vw(70);
        left: vw(98);

        .quiz__pag__item{
            cursor: pointer;
            font-size: vw(20);
            color: white;
            font-family: 'Montserrat', sans-serif;
            font-weight: 500;
            text-transform: uppercase;
            margin-top: vw(45);

            &:first-child{
                margin-top: 0;
            }

            &.active{
                color: #57C54E;
            }
        }
    }
}

.guiz__page{
 
    .quiz__page__content{
        height: vw(1005);
        position: relative;

        p.header{
            text-align: center;
            padding-top: vw(70);
            font-size: vw(27);
            font-family: 'Montserrat', sans-serif;
            font-weight: 550;
            text-transform: uppercase;
        }
    }
}

