    }
    
} */

.home__item__wrapper{
    /* display: grid;
    grid-template-columns: vw(214) vw(214) vw(214);
    justify-content: space-between; */
    width: vw(800);
    /* row-gap: vw(30);
    column-gap: vw(30); */
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    row-gap: vw(30);
    margin-top: vw(40);
    margin-left: vw(140);

    .home__item{
        width: vw(214);
        height: vw(234);
        border: vw(1) solid #BABABA;
        cursor: pointer;

        .home__item__images{
            width: vw(214);
            height: vw(189);

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        p.title{
            text-align: center;
            font-size: vw(15);
            font-family: 'Montserrat', sans-serif;
            font-weight: 450;
            padding-top: vw(15);
        }
    }
}

.btn__quiz--next{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: vw(47);
    right: 0;

    p{
        font-size: vw(27);
        font-family: 'Montserrat', sans-serif;
        font-weight: 500;
        text-transform: uppercase;
    }

    .btn__next__images{
        width: vw(27);
        height: vw(16);
        margin-left: vw(5);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}